<template>
    <div class="cart">
        <Header title="购物车" :edit="true"/>
        <CartDetails v-if="isShow" :changeShow="changeShow" />
        <Blank v-else />
        <Footer />
    </div>
</template>
<script>
import Footer from "../../components/Footer"
import Header from "@/components/Header.vue";
import CartDetails from "./components/CartDetails"
import { ref } from "vue"
import Blank from "./components/Blank.vue"
import { useStore } from "vuex"
import { onMounted } from "vue";
export default {
    components: {
        Footer,
        Header,
        CartDetails,
        Blank
    },
    setup() {
        let store = useStore()
        let isShow = ref(true)

        let init = () => {
            if (store.state.cartList.length == 0) {
                isShow.value = false
            }
        }

        const changeShow=()=>{
            isShow.value = false
        }

        onMounted(()=>{
            init()
        })

        return {
            isShow,
            changeShow
        }
    }
}
</script>
<style lang="less" scoped>
.cart {
    display: flex;
    flex-flow: column;
    height: 100%;

    .content {
        flex: 1;
        overflow-y: auto;
    }
}
</style>