<template>
    <div class="cartDetails">
        <!-- 商品列表 -->
        <div class="content">
            <van-checkbox-group v-model="result" @change="groupChange">
                <div v-for="(i, index) in store.state.cartList" :key="index">
                    <FoodAdd :item="i" :showCheckbox="true" :onChange="onchange" />
                </div>
            </van-checkbox-group>
        </div>
        <!-- 结算 -->
        <van-submit-bar :price="allPrice" v-if="isDelete" button-text="结算" class="submit-all" @submit="onSubmit">
            <van-checkbox checked-color="#ffc400" @click="choseAll" v-model="checked">全选</van-checkbox>
        </van-submit-bar>

        <!-- 删除 -->
        <div class="buy" v-else>
            <div class="left">
                <van-checkbox checked-color="#ffc400" v-model="checked" @click="choseAll">全选</van-checkbox>
            </div>
            <div class="delete" @click="deleteClick">删除</div>
        </div>
    </div>
</template>
<script>
import emitter from "@/common/js/evenbus"
import { useStore } from "vuex"
import FoodAdd from "@/components/FoodAdd"
import { reactive, toRefs } from "vue"
import { onMounted, computed } from "vue"
import { showToast } from 'vant';
import { useRouter } from "vue-router"
export default {
    components: {
        FoodAdd
    },
    props: ["changeShow"],
    setup(props) {
        let data = reactive({
            result: [],
            checked: true,
            isDelete: true
        })
        const store = useStore()

        const onchange = (value, detail) => {
            store.state.cartList.map(item => {
                if (item.id == detail.name) {
                    item.num = value
                }
            })
        }

        const init = () => {
            data.result = store.state.cartList.map(item => item.id)
        }

        onMounted(() => {
            init()
        })

        let update = (type) => {
            return store.state.cartList.filter(item => {
                return type == 2
                    ? data.result.includes(item.id)
                    : !data.result.includes(item.id)
            })
        }
        // 结算的函数
        const onSubmit = () => {
            if (data.result.length !== 0) {
                store.commit("PAY", update(2))
                console.log(" data.result", data.result);
                router.push({ path: "/createorder", query: { list: data.result } })
            } else {
                showToast("请选择要结算的商品")
            }
        }

        // 全选的点击
        const choseAll = () => {
            if (data.result.length !== store.state.cartList.length) {
                init()
            } else {
                data.result = []
            }
        }

        // 每一个复选框
        const groupChange = (result) => {
            if (result.length === store.state.cartList.length) {
                data.checked = true
            } else {
                data.checked = false
            }
            data.result = result
        }

        // 总价
        const allPrice = computed(() => {
            let countList = store.state.cartList.filter(item => {
                return data.result.includes(item.id)
            })

            let sum = 0;

            countList.forEach(item => {
                sum += item.num * item.price
            })

            return sum * 100
        })

        // 编辑调用的 事件总线函数
        emitter.on("edit", () => {
            data.isDelete = !data.isDelete
        })

        // 删除方法
        const deleteClick = () => {
            if (data.result.length) {
                store.commit("DELETE", update(1))

                data.result = []
                store.commit("EDIT", "delete")
                data.isDelete = true

                if (store.state.cartList.length === 0) {
                    props.changeShow()
                }
            } else {
                showToast("没有要删除的商品")
            }
        }

        const router = useRouter()

        return {
            store,
            ...toRefs(data),
            onchange,
            onSubmit,
            choseAll,
            groupChange,
            allPrice,
            deleteClick
        }
    }
}
</script>
<style scoped lang="less">
.cartDetails {
    font-size: 14px;
    flex: 1;
    position: relative;
    overflow-y: auto;
    padding: 20px;
    height: 70%;

    .submit-all {
        // 全选  暂时没有页面 所有先不写
        position: fixed;
        bottom: 49px;
    }

    .buy {
        position: fixed;
        bottom: 49px;
        right: 0;
        display: flex;
        justify-content: space-between;
        width: 100%;
        background-color: #fff;
        border-radius: 10px;
        height: 50px;
        align-items: center;
        padding: 0 16px;
        box-sizing: border-box;

        .left {
            display: flex;
            align-items: center;
        }

        .delete {
            color: #fff;
            background-color: #ffc400;
            border-radius: 10px;
            font-size: 14px;
            font-weight: 700;
            width: 110px;
            height: 40px;
            text-align: center;
            line-height: 40px;
        }
    }

}
</style>